import { setupListeners } from '@reduxjs/toolkit/dist/query';
import { configureStore } from '@reduxjs/toolkit';
import logger from 'redux-logger';
import {
  // persistStore,
  // persistReducer,
  FLUSH,
  REHYDRATE,
  PAUSE,
  PERSIST,
  PURGE,
  REGISTER,
} from 'redux-persist';
// import storage from 'redux-persist/lib/storage';
import { phonebookReducer } from './app/app-phonebook-reducer';
import { contactsApi } from './app/operation';
// const phonebookPersistConfig = {
//   key: 'contact',
//   storage,
//   blacklist: ['filter'],
// };
// const persisteReducer = persistReducer(
//   phonebookPersistConfig,
//   phonebookReducer,
// );
export const store = configureStore({
  reducer: {
    contacts: phonebookReducer,
    [contactsApi.reducerPath]: contactsApi.reducer,
  },
  middleware: getDefaultMiddleware =>
    getDefaultMiddleware(
      {
        serializableCheck: {
          ignoredActions: [FLUSH, REHYDRATE, PAUSE, PERSIST, PURGE, REGISTER],
        },
      },
      phonebookReducer.middleware,
      contactsApi.middleware, //   ПЕРЕВІРИТИ ЧИ ВПЛИВАЄ НА ПЕРВІРКУ
    ).concat(logger),

  devTools: process.env.NODE_ENV !== 'production',
});

setupListeners(store.dispatch);
